// Styling for homepage elements

.cover-text {
  margin-bottom: $spacing-unit;
  @include media-query($on-laptop) {
    height: auto;
  }
}

.cover {
  margin-bottom: $spacing-unit - 8px;
  //padding-bottom: 8px;
  border-bottom: 3px solid $background-color;
  &:hover {
    border-bottom: 3px solid $brand-dark;
  }
  margin-right: $spacing-unit/2;
  margin-left: $spacing-unit/2;

  @include media-query($on-laptop) {
    float: none !important;
  }
}

.caption {
  font-size: 0.9em;
  padding-top: 0.5em;
  color: $brand-color;
  font-family: $heading-font-family;
}

.left {
  width: 40%;
  float: left;
}

.left-sm {
  width: 300px;
  float: left;
}

.right {
  width: 250px;
  float: right;
}

.right-sm {
  width: 200px;
  float: right;
}
